import React, { Component } from 'react';
import Profiles from "./Profiles";
import Ellipse3 from "../images/Ellipse3.jpg";
import Ellipse2 from "../images/Ellipse2.jpg";
import Ellipse4 from "../images/Ellipse4.jpg";

 class Team extends Component {
  render() {
    return (
      <div className="team">
        <div className="team_text">
          <h2>Our<span className="text_color">Team</span></h2>
          <p>meet our team now</p>
        </div>
        <div className="team_container">
          <Profiles name="Robert Fox" pic={Ellipse3}/>
          <Profiles name="Albert Flores" pic={Ellipse2}/>
          <Profiles name="Robert Fox" pic={Ellipse4}/>
        </div>
        <div className="text_color">
          <p >Scroll Down</p>
          <i className="fas fa-angle-double-down"></i>
        </div>
      </div>
    )
  }
}
export default Team ;
