import React, { Component } from 'react'
import { BrowserRouter as Router, Route} from "react-router-dom";
import Navigation from "./Navigation";
import UgliDesigner from "./UgliDesigner";
import Portfolio from "./Portfolio";
import Team from "./Team";
import ContactUs from "./ContactUs";
import './style.scss';

class Main extends Component{
  state={
    page: true
  }

  
  render(){
    const handleClick = ()=>{
      this.setState({page:false});
    }
    return(
      <Router>
        <div className="color1">
          <div className="top_blur"></div>
          <Navigation changeColor={this.change} handleClick={handleClick}/>
          {this.state.page? <UgliDesigner />:null}
          <Route exact path="/" component={UgliDesigner} />
          <Route path="/portfolio" component={Portfolio} />
          <Route path="/team" component={Team} />
          <Route path="/contactus"  component={ContactUs} />
          <div className="down_blur"></div>
        </div>
      </Router>
    )
  }
}

export default Main;
